import { SwipeableDrawer } from '@material-ui/core';
import React,{useState} from 'react'
import TinderCard from 'react-tinder-card';
import "./Cards.css"

import y1 from './pic1.gif'
import y2 from './pic2.gif'

function Cards() {
  const [art,setArt]=useState([

    {type:'gif',url:y1},
    {type:'pixel_isometric',url:y2},
  ]);


  const swiped=(a,b)=>{console.log(b+" will be removed");};
  const outOfFrame=(v)=>{console.log(v+"out of view");};

  return (
    <div className="cards">
     <div className="cards__cardContainer">
     {art.map(
        (x)=>(
        <TinderCard className="swipe" key={x.type} preventSwipe={["up","down"]} onSwipe={(s)=>swiped(s,x.type)} onCardLeftScreen={()=>outOfFrame(x.type)}>
            <div style={{backgroundImage:`url(${x.url})`}}  className="card">
              <h3>{x.type}</h3>

            </div>
        </TinderCard>
      ))}
     </div>
    </div>
  )
}

export default Cards
