import React from 'react'
import './Clickables.css';
import ReplayIcon from '@material-ui/icons/Replay';
import CloseIcon from '@material-ui/icons/Close';
import StarRateIcon from '@material-ui/icons/StarRate';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FlashOnIcon from '@material-ui/icons/FlashOn';

import x1 from './charizard.gif'
import x2 from './charLeft.png'
import x3 from './mikuMid.gif'
import x4 from './charRight.gif'
import x5 from './naru.gif'
import { IconButton } from '@material-ui/core';

function Clickables() {
  return (
    <div className="buttons">
      <IconButton  className="repeat">
        <img src={x1} alt="charizard" />
        <ReplayIcon fontSize="large" className="repeat_icon" />
      </IconButton>
      <IconButton className="close">
        <img src={x2} alt="charmendar_left" />
        <CloseIcon fontSize="large" className="close_icon" />
      </IconButton>
      
      <IconButton  className="star">
        <img src={x3} alt="miku" />
        <StarRateIcon fontSize="large" className="star_icon" />ðŸ‘Œ
      </IconButton>
      <IconButton  className="fav">
        <img src={x4} alt="charmendar_right" />
        <FavoriteIcon fontSize="large" className="fav_icon" />
      </IconButton>
      <IconButton  className="flashing">
        <img src={x5} alt="yippie" />
        <FlashOnIcon fontSize="large" className="flash_icon" />
      </IconButton>
      
    </div>
  )
}

export default Clickables
